const EmotionTable = ({ emotions }) => {

  return (
    <div>
      <table className="table table-bordered">
        <tbody>
          { 
            Object.entries(emotions).map((emotion) => {
              return (
                <tr>
                  <td>{emotion[0]}</td>
                  <td>{emotion[1]}</td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
    </div>
  )

} 

export default EmotionTable;